import {LuckyDraw} from "@/configure/bpos";

export const drawAddress="0x7B4e6f7CBA9E441eC87742afFC2bfbfe8F1771eb"


//抽奖
export async function luckyDraw(wallet,name,callback) {
    const contract = new wallet.web3.eth.Contract(LuckyDraw.abi, drawAddress)
    await luckyDrawEstimateGas(wallet,name)
    function _luckyDraw(web3,contract,address,name, callback) {
        return new Promise((resolve, reject) => {
            contract.methods.luckyDraw(name).send({from: address}).on('transactionHash', function (hash) {
                callback({
                    message: hash,
                    options: {
                        key: new Date().getTime() + Math.random(),
                        variant: 'success'
                    },
                    hash
                });
            }).on('receipt', function (receipt) {
                console.log(receipt);
                resolve(receipt)
            }).on('error', function (error) {
                console.log(error)
                reject(error)
            }).catch((error) => {
                console.log(error)
                reject(error)
            })
        })
    }

    return  _luckyDraw(wallet.web3,contract, wallet.address,name, callback)
}

//获取参与人数
export async function getAddress(wallet) {
    const contract = new wallet.web3.eth.Contract(LuckyDraw.abi, drawAddress)
    return contract.methods.getAddress().call({from: wallet.address})
}

//获取参与人数
export async function getAddressInfo(wallet) {
    const contract = new wallet.web3.eth.Contract(LuckyDraw.abi, drawAddress)
    return contract.methods.getAddressInfo().call({from: wallet.address})
}

//获取参与人数
export async function luckyDrawEstimateGas(wallet, name) {
    const contract = new wallet.web3.eth.Contract(LuckyDraw.abi, drawAddress)
    let web3=wallet.web3
    console.log(wallet.address)
    console.log(contract.methods.luckyDraw(name).encodeABI())
    let txObject = {
        from: wallet.address,
        to: drawAddress,
        data:contract.methods.luckyDraw(name).encodeABI()
    }
    return new Promise(async (resolve, reject) => {
        web3.eth.estimateGas(txObject).then(res => {
            resolve(res)
        }).catch((err) => {
            reject(err)
        })
    })
}

